body {
    margin: 0;
    padding: 0;
    background-color: #f1f1f1;
    line-height: 1.2;
    font-family: arial;
    color: #333;
    min-width: 1000px;
}
* {
    box-sizing: border-box;
}
.app {
    padding: 15px;
    /* display: flex; */
    /* align-items: flex-start; */
}
.app__title {
    margin: 0 0 30px;
    font-family: cursive;
}
.title-cross {
    font-size: 24px;
    font-weight: 700;
    font-style: italic;
    margin-bottom: 15px;
}
a {
    text-decoration: underline;
}
a:hover {
    color: #e74c3c;
}

/* ссылка вернуться назад к списку */
.return-back-list {
    text-decoration: underline;
    margin-bottom: 30px;
    cursor: pointer;
    padding-left: 25px;
}
.return-back-list:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 25px;
    height: 25px;
    background: url(/return.svg) 0 0 no-repeat;
    background-size: 25px;
}
.return-back-list:hover {
    text-decoration: none;
    color: #e74c3c;
}

.message {
    margin-bottom: 30px;
}

