.status_done {
    width: 12px;
    height: 16px;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-image: url(/ok.svg);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.status_edit {
    width: 16px;
    height: 16px;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-image: url(/list-edit.svg);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.status_not-resolved {
    width: 16px;
    height: 16px;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-image: url(/list.svg);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}

